/**
 * API配置中心
 * 集中管理API的基础配置
 */

// 开发环境API基础URL
const DEV_BASE_URL = 'http://dev-api.example.com';

// 生产环境API基础URL
const PROD_BASE_URL = 'https://api.example.com';

// API版本号
const API_VERSION = 'v1';

// 当前环境
const ENV = process.env.NODE_ENV || 'development';

// 导出配置
export default {
  // 根据环境获取基础URL
  get BASE_URL() {
    return ENV === 'development' ? DEV_BASE_URL : PROD_BASE_URL;
  },
  
  // API版本号
  VERSION: API_VERSION,
  
  // 请求超时时间(ms)
  TIMEOUT: 30000,
  
  // 是否开启调试日志
  DEBUG: ENV === 'development',
  
  // 状态码消息映射
  ERROR_CODE: {
    400: '请求参数错误',
    401: '未登录或登录已过期',
    403: '没有权限进行此操作',
    404: '请求的资源不存在',
    500: '服务器内部错误',
    502: '网关错误',
    503: '服务不可用',
    504: '网关超时'
  }
}; 